	module.exports = {"data":{"markdownRemark":{"html":"<section>\n<div class=\"container\">\n<h3 class=\"page-header\">Getting started</h3>\n<h4><span class=\"h4-light\">Using BHP Digital Design System (DDS) Components</span></h4>\n<p>Our components are a collection of front-end interface elements that can be reused across the BHP browser-based products.</p>\n<p>We offer three variations of components: React, HTML, and Angular variations, where applicable. If your project can utilise React components then it is recommended to use these going forward. Using React components in your project enables BHP to adhere to a consistent interactive experience that will scale over time and help reduce technical debt throughout our web-based product lines.</p>\n<h4><span class=\"h4-light\">BHP UI NPM module</span></h4>\n<p>An NPM module named BHP UI (<code>bhp-ui</code> on NPM) is <a href=\"https://github.com/BHP-DevHub/bhp-ui\">available on Github</a>. Instructions for installing this library are given at that repository. This contains two custom Bootstrap themes, light and dark, demonstrated on this website, along with SCSS files that can be used to build custom versions of the themes.</p>\n<h4><span class=\"h4-light\">BHP UI React</span></h4>\n<p>For projects using React, an additional NPM module, BHP UI React (<code>bhp-ui-react</code> on NPM) is <a href=\"https://github.com/BHP-DevHub/bhp-ui-react\">available on Github</a> containing the React components documented on this website.</p>\n<p>Our BHP widget repository contains the common React widgets used by our web-based products. You will need private Github access to this repository to begin working with BHP widgets at this stage. Please contact your dev manager to enable this. Always visit the Github repository for latest updates.</p>\n<h5>Starter app</h5>\n<p>To ease the process of starting a new BHP app, a starter app template has <a href=\"https://github.com/BHP-DevHub/bhp-react-app\">been made available on Github</a>. Developers can download a zip copy of this template from that repository and follow the instructions in the README.md file to get a basic skeleton React project using the BHP UI styles and BHP UI React components.</p>\n<h4><span class=\"h4-light\">Contributing</span></h4>\n<p>We encourage all BHP developers to contribute improvements, bug reports, and other feedback to the two libraries. Feedback should be provided via the Github Issues section of the corresponding repositories.</p>\n<h4><span class=\"h4-light\">Browser support</span></h4>\n<p>Our React and HTML components are designed to work as intended in the following browsers:</p>\n<div class=\"d-flex text-center\">\n  <div class=\"d-flex flex-column m-3\">\n    <div><img src=\"/edge-6e9fe0bd9d0227db548424b249aced10.svg\" width=\"50\" /></div>\n    <div><strong>Internet Explorer 10</strong></div>\n    <div>(or later)</div>\n  </div>\n  <div class=\"d-flex flex-column m-3\">\n    <div><img src=\"/chrome-e9ba686baad6bdd2f5e7835b4e3c0ca8.svg\" width=\"50\" /></div>\n    <div><strong>Chrome</strong></div>\n    <div>(latest two versions)</div>\n  </div>\n  <div class=\"d-flex flex-column m-3\">\n    <div><img src=\"/firefox-26e18c1e9a95790506e073b0eb6eb3f5.svg\" width=\"50\" /></div>\n    <div><strong>Firefox</strong></div>\n    <div>(latest two versions)</div>\n  </div>\n  <div class=\"d-flex flex-column m-3\">\n    <div><img src=\"/safari-029e780d4fc61bb1560a73ebf2fa6c09.svg\" width=\"50\" /></div>\n    <div><strong>Safari</strong></div>\n    <div>(latest two versions)</div>\n  </div>\n</div>\n</div>\n</section>","fields":{"slug":"/ui-components/getting-started/"},"frontmatter":{"title":"Getting started","category":"UI components"}}},"pathContext":{"slug":"/ui-components/getting-started/"}}

